import { Component, OnInit, OnDestroy, Input,trigger, transition, style, animate  } from '@angular/core';
import {Http} from '@angular/http' 
import {GrobotDataService} from '../../../_shared/grobot-data.service'
import {SocketService} from '../../../_shared/socket.service'
import {ActivatedRoute} from '@angular/router'
declare var CanvasJS:any;
@Component({
  selector: 'grobot-live-chart',
  animations: [
    trigger(
      'enterAnimation', [
        transition(':enter', [
          style({transform: 'translateX(100%)', opacity: 0}),
          animate('500ms', style({transform: 'translateX(0)', opacity: 1}))
        ]),
        transition(':leave', [
          style({transform: 'translateX(0)', opacity: 1}),
          animate('500ms', style({transform: 'translateX(100%)', opacity: 0}))
        ])
      ]
    )
  ],
  templateUrl: './grobot-live-chart.component.html',
  styleUrls: ['./grobot-live-chart.component.css']
})
export class GrobotLiveChartComponent implements OnInit,OnDestroy {
  // @Input('show') showArray = [false,false,false,false,false]

  vpd:any = [];
  soil:any = [];
  temp:any = [];
  humi:any = [];
  light:any = [];

  vpdChart:any;
  soilChart:any;
  tempChart:any;
  humiChart:any;
  lightChart:any;

  chart:any;
  sensor:any;
  dataChart:any = [];

  xVal = 0;
	yVal = 100;	
  updateInterval = 100;
	dataLength = 50; // number of dataPoints visible at any point

  intervalLoop:any;
  constructor(private grobotData:GrobotDataService,private route:ActivatedRoute,private http:Http,private io:SocketService) {}

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.

    clearInterval(this.intervalLoop);
    console.log("clear intervalLoop")

  }

  ngOnInit() {

    
    console.log("on init")

    this.route.queryParams.subscribe(param=>{
      this.ngOnDestroy();
      this.sensor = param.sensor;
      this.dataChart = [];
      
      this.http.get(this.io.hostname + "/api/shortlogger").subscribe(data=>{
        let shortLogger = data.json()
        shortLogger.forEach(d=>{
          let datetime = d.date + " " + d.time

          this.dataChart.push({
            x: new Date(datetime),
					  y: d[this.sensor]
          })
          
        })

        this.chart = new CanvasJS.Chart("chart",{
          title :{
            text: this.fullString(this.sensor)+ " Real-time graph"
          },			
          animationEnabled: true,
          axisY :{
            suffix: this.getSuffix(this.sensor)
          },
          data: [{
            type: "line",
            dataPoints: this.dataChart 
          }]
        })

      this.chart.render();

      this.intervalLoop = setInterval( ()=>{
          this.updateChart(1);
        }, 3000)
      })

     
      

    })
    
    
    
    
  }

  updateChart = function (count) {
			count = count || 1;
			// count is number of times loop runs to generate random dataPoints.
			
			for (var j = 0; j < count; j++) {	
				this.dataChart.push({
					x: new Date(this.grobotData.datetime),
					y: this.grobotData.sensor[this.sensor]
				});
			};

			if (this.dataChart.length > this.dataLength)
			{
				this.dataChart.shift();			
			}

      this.chart.render();
		}

    RenderAll():void {
        this.chart.render();
    }

    fullString(sensor):string {
      if(sensor == "vpd") return "VPD";
      else if(sensor == "soil") return "Soil moisture";
      else if(sensor == "temp") return "Temperature";
      else if(sensor == "humi") return "Humidity";
      else if(sensor == "light") return "Light";
      else return "";
    }

    

    getSuffix(sensor):string {
      if(sensor == "vpd") return " Pa";
      else if(sensor == "soil") return " %";
      else if(sensor == "temperature") return " C";
      else if(sensor == "humidity") return " %";
      else if(sensor == "light") return " lux";
      else return "";
    }

}



