import {Component, OnInit, Input, OnDestroy, NgZone} from '@angular/core';
import {Router} from '@angular/router'
import {FakeDataSource,salesChartDemoOptions} from "../../../+graphs/+flot-charts/flot-examples";
import {GrobotDataService} from '../../../_shared/grobot-data.service'



declare var JustGage:any;

@Component({
  selector: 'grobot-live-sensor',
  templateUrl: './live-sensor.component.html',
  styleUrls: ['./live-sensor.component.css']
})
export class LiveSensorComponent implements OnInit ,OnDestroy{
  @Input() public liveSwitch = false;

  public liveStats: Array<any>;
  public sensorArray:any = [false,true,false,false,false];
  chartOption = salesChartDemoOptions
  vpdGauge:any;
  soilGauge:any;
  humiGauge:any;
  tempGauge:any;
  lightGauge:any;
  constructor(private zone: NgZone, private grobotData:GrobotDataService,private router:Router) { }

   ngOnInit() {

    this.vpdGauge = new JustGage({
      id: "mini-vpdGauge",
      defaults: this.vpdGaugeData
    });

    this.soilGauge = new JustGage({
      id: "mini-soilGauge",
      defaults: this.soilGaugeData
    });

    this.humiGauge = new JustGage({
      id: "mini-humiGauge",
      defaults: this.humiGaugeData
    });

     this.tempGauge = new JustGage({
      id: "mini-tempGauge",
      defaults: this.tempGaugeData
    });

    this.lightGauge = new JustGage({
      id: "mini-lightGauge",
      defaults: this.lightGaugeData
    });

    setInterval(()=>{
      this.vpdGauge.refresh(this.grobotData.sensor.vpd)
      this.soilGauge.refresh(this.grobotData.sensor.soil)
      this.humiGauge.refresh(this.grobotData.sensor.humidity)
      this.tempGauge.refresh(this.grobotData.sensor.temperature)
      // this.lightGauge.refresh(this.grobotData.sensor.light)
    },1000)
    
    this.updateStats()
  }

  sensorShow(value):void {
    for(var i = 0 ; i < this.sensorArray.length ; i++){
      this.sensorArray[i] = false;
    }
    this.sensorArray[value] = true;

    if(value == 0 ){
      // this.router.navigateByUrl("graph")
       this.router.navigate(['home/graph'],{ queryParams: { sensor: "vpd" } });
    }
    else if(value == 1){
        this.router.navigate(['home/graph'],{ queryParams: { sensor: "soil" } });
    }
    else if (value == 2){
       this.router.navigate(['home/graph'],{ queryParams: { sensor: "humi" } });
    }
    else if (value == 3){
       this.router.navigate(['home/graph'],{ queryParams: { sensor: "temp" } });
    }
  }

  updateStats() {
    this.liveStats = [this.grobotData.vpdLog]
    console.log(this.liveStats)
  }

  private interval;

  toggleSwitch() {

    if (this.liveSwitch) {
      this.interval = setInterval(()=>{
        this.updateStats()
      }, 1000)
    } else {
      clearInterval(this.interval);
    }
  }

  ngOnDestroy() {
    this.interval &&  clearInterval(this.interval);
  }

  liveStatsChartOptions = {
    yaxis: {
      min: 0,
      max: 5000
    },
    xaxis: {
      mode : "time",
      tickLength : 5
    },
    colors: ['rgb(87, 136, 156)'],
    grid: {
      show: true,
      hoverable: true,
      clickable: true,
      borderWidth: 0,
    },
    series: {
      lines: {
        lineWidth: 1,
        fill: true,
        fillColor: {
          colors: [
            {
              opacity: 0.4
            },
            {
              opacity: 0
            }
          ]
        },
        steps: false

      }
    }
  }

  public vpdGaugeData= {
          value: 2200,
          min: 0,
          max: 5000,
          symbol: ' Pa',
          pointer: false,
          title: "Custom Width",
          label: "",
          gaugeWidthScale: 0.6,
          
        levelColors: [
          "#a57225"
        ],
        counter: true,
        relativeGaugeSize: true,
        donut: true
  }

  public soilGaugeData= {
          value: 2200,
          min: 0,
          max: 100,
          symbol: ' %',
          pointer: false,
          title: "Custom Width",
          label: "",
          gaugeWidthScale: 0.6,
          
        levelColors: [
          "#71843f"
        ],
        counter: true,
        relativeGaugeSize: true,
        donut: true
  }

    public humiGaugeData= {
          value: 2200,
          min: 0,
          max: 100,
          symbol: ' %',
          pointer: false,
          title: "Custom Width",
          label: "",
          gaugeWidthScale: 0.6,
          
        levelColors: [
          "#57889c"
        ],
        counter: true,
        relativeGaugeSize: true,
        donut: true
  }

public tempGaugeData= {
          value: 50,
          min: 0,
          max: 80,
          symbol: ' C',
          pointer: false,
          title: "Custom Width",
          label: "",
          gaugeWidthScale: 0.6,
          
        levelColors: [
          "#f48342"
        ],
        counter: true,
        relativeGaugeSize: true,
        donut: true
  }

  public lightGaugeData= {
          value: 50,
          min: 0,
          max: 600,
          symbol: ' lux',
          pointer: false,
          title: "Custom Width",
          label: "",
          gaugeWidthScale: 0.6,
          
        levelColors: [
          "#f48342"
        ],
        counter: true,
        relativeGaugeSize: true,
        donut: true
  }

}



