import { Component, OnInit,Input } from '@angular/core';
import {GrobotDataService} from '../../../_shared/grobot-data.service'
declare var JustGage:any;

@Component({
  selector: 'grobot-just-gauge',
  templateUrl: './grobot-just-gauge.component.html',
  styleUrls: ['./grobot-just-gauge.component.css']
})
export class GrobotJustGaugeComponent implements OnInit {
  @Input('show') show:any = [false,false,false,false,false]

  public vpdGaugeData= {
          value: 0,
          min: 0,
          max: 5000,

          symbol: ' Pa',
          pointer: true,
          pointerOptions: {
            toplength: -15,
            bottomlength: 10,
            bottomwidth: 12,
            color: '#8e8e93',
            stroke: '#ffffff',
            stroke_width: 3,
            stroke_linecap: 'round'
        },
        levelColors: [
          "#42adf4",
          "#41f4ca",
          "#3fff5c",
          "#c5ff5b",
          "#f9ab57",
          "#fc6e55"
        ],
        gaugeWidthScale: 0.6,
        counter: true,
  }

  public soilGaugeData= {
          value: 0,
          min: 0,
          max: 100,
          decimals: 1,
          symbol: ' %',
          pointer: true,
          pointerOptions: {
            toplength: -15,
            bottomlength: 10,
            bottomwidth: 12,
            color: '#8e8e93',
            stroke: '#ffffff',
            stroke_width: 3,
            stroke_linecap: 'round'
        },
        levelColors: [
          "#42adf4",
          "#41f4ca",
          "#3fff5c",
          "#c5ff5b",
          "#f9ab57",
          "#fc6e55"
        ],
        gaugeWidthScale: 0.6,
        counter: true,
  }

  public tempGaugeData= {
          value: 0,
          min: 0,
          max: 80,
          symbol: ' C',
          pointer: true,
          pointerOptions: {
            toplength: -15,
            bottomlength: 10,
            bottomwidth: 12,
            color: '#8e8e93',
            stroke: '#ffffff',
            stroke_width: 3,
            stroke_linecap: 'round'
        },
        levelColors: [
          "#42adf4",
          "#41f4ca",
          "#3fff5c",
          "#c5ff5b",
          "#f9ab57",
          "#fc6e55"
        ],
        gaugeWidthScale: 0.6,
        counter: true,
  }

public lightGaugeData= {
          value: 0,
          min: 0,
          max: 600,
          symbol: ' Lux',
          pointer: true,
          pointerOptions: {
            toplength: -15,
            bottomlength: 10,
            bottomwidth: 12,
            color: '#8e8e93',
            stroke: '#ffffff',
            stroke_width: 3,
            stroke_linecap: 'round'
        },
        levelColors: [
          "#42adf4",
          "#41f4ca",
          "#3fff5c",
          "#c5ff5b",
          "#f9ab57",
          "#fc6e55"
        ],
        gaugeWidthScale: 0.6,
        counter: true,
  }

  vpdGauge:any;
  soilGauge:any;
  tempGauge:any;
  humiGauge:any;
  lightGauge:any;
  constructor(private grobotData:GrobotDataService) { 
  
  }
  
  ngOnInit() {

    this.vpdGauge = new JustGage({
      id: "vpdGauge",
      defaults: this.vpdGaugeData
    })
    this.soilGauge = new JustGage({
      id: "soilGauge",
      defaults: this.soilGaugeData
    })

    this.tempGauge = new JustGage({
      id: "tempGauge",
      defaults: this.tempGaugeData
    })
    this.humiGauge = new JustGage({
      id: "humiGauge",
      defaults: this.soilGaugeData
    })
    this.lightGauge = new JustGage({
      id: "lightGauge",
      defaults: this.lightGaugeData
    })

    setInterval(()=>{
      this.vpdGauge.refresh(this.grobotData.sensor.vpd);
      this.soilGauge.refresh(this.grobotData.sensor.soil);

      this.tempGauge.refresh(this.grobotData.sensor.temperature);
      this.humiGauge.refresh(this.grobotData.sensor.humidity);
      this.lightGauge.refresh(this.grobotData.sensor.light);
    },2000)
  }

}



