import { Component, OnInit } from '@angular/core';
declare var JustGage:any;

@Component({
  selector: 'just-gauge',
  templateUrl: './just-gauge.component.html',
  styleUrls: ['./just-gauge.component.css']
})
export class JustGaugeComponent implements OnInit {
   gaugeData= {
          id: "gg1",
          value: 2200,
          min: 0,
          max: 5000,
          symbol: ' Pa',
          pointer: true,
          pointerOptions: {
            toplength: -15,
            bottomlength: 10,
            bottomwidth: 12,
            color: '#8e8e93',
            stroke: '#ffffff',
            stroke_width: 3,
            stroke_linecap: 'round'
        },
        levelColors: [
          "#42adf4",
          "#41f4ca",
          "#3fff5c",
          "#c5ff5b",
          "#f9ab57",
          "#fc6e55"
        ],
        gaugeWidthScale: 0.6,
        counter: true,
  }

  gauge:any;
  constructor() { }

  ngOnInit() {
    this.gauge = new JustGage({
      id: "gg1",
      defaults: this.gaugeData
    })
  }

}



