import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { homeRouting } from './home.routing';
import {SmartadminModule} from "../shared/smartadmin.module";
import {HomeComponent} from "./home.component";
import {LiveFeedsComponent} from '../+dashboard/+analytics/live-feeds/live-feeds.component'
import {LiveStatsComponent} from '../+dashboard/+analytics/live-feeds/live-stats.component'
import {FlotChartModule} from "../shared/graphs/flot-chart/flot-chart.module";
import {JustGaugeComponent} from '../+graphs/just-gauge/just-gauge.component';
import { LiveFeedComponent } from './live-feed/live-feed.component';
import { LiveSensorComponent } from './live-feed/live-sensor/live-sensor.component';
import { GrobotJustGaugeComponent } from './live-feed/grobot-just-gauge/grobot-just-gauge.component';
import { GrobotLiveChartComponent } from './live-feed/grobot-live-chart/grobot-live-chart.component'
import {TimeModule} from './time/time.module'
@NgModule({
  imports: [
    CommonModule,
    homeRouting,
    SmartadminModule,
    FlotChartModule,
    TimeModule
  ],
  declarations: [HomeComponent, 
  LiveFeedsComponent,
  LiveStatsComponent,
  JustGaugeComponent, 
  LiveFeedComponent, LiveSensorComponent, 
  GrobotJustGaugeComponent,
   GrobotLiveChartComponent]
})
export class HomeModule { }



